import { NextRequest } from "next/server";
import jwt from "jsonwebtoken";

const JWT_SECRET =
  process.env.JWT_SECRET ||
  "petconnect_super_secret_key_change_this_in_production_minimum_32_characters_random";

interface AuthResult {
  authenticated: boolean;
  userId?: string;
  email?: string;
  role?: string;
  error?: string;
}

/**
 * Authentifie l'utilisateur √† partir du token JWT dans les headers
 */
export async function authenticateUser(
  request: NextRequest
): Promise<AuthResult> {
  try {
    // R√©cup√©rer le token depuis les headers
    const authHeader = request.headers.get("Authorization");

    if (!authHeader || !authHeader.startsWith("Bearer ")) {
      console.log("‚ùå Aucun token fourni");
      return {
        authenticated: false,
        error: "Token manquant",
      };
    }

    // Extraire le token
    const token = authHeader.substring(7); // Enlever "Bearer "

    // V√©rifier et d√©coder le token
    const decoded = jwt.verify(token, JWT_SECRET) as {
      userId?: string;
      id?: string;
      email?: string;
      role?: string;
    };

    // Le token peut avoir soit 'userId' soit 'id'
    const userId = decoded.userId || decoded.id;

    if (!userId) {
      console.log("‚ùå Token invalide: pas d'userId");
      return {
        authenticated: false,
        error: "Token invalide",
      };
    }

    console.log("‚úÖ Utilisateur authentifi√©:", userId);

    return {
      authenticated: true,
      userId,
      email: decoded.email,
      role: decoded.role,
    };
  } catch (error: any) {
    console.error("‚ùå Erreur authentification:", error.message);

    if (error.name === "TokenExpiredError") {
      return {
        authenticated: false,
        error: "Token expir√©",
      };
    }

    if (error.name === "JsonWebTokenError") {
      return {
        authenticated: false,
        error: "Token invalide",
      };
    }

    return {
      authenticated: false,
      error: "Erreur d'authentification",
    };
  }
}

/**
 * Extrait le token depuis les cookies (alternative)
 */
export function getTokenFromCookies(request: NextRequest): string | null {
  const token = request.cookies.get("token")?.value;
  return token || null;
}

/**
 * V√©rifie si l'utilisateur a un r√¥le sp√©cifique
 */
export function hasRole(authResult: AuthResult, requiredRole: string): boolean {
  return authResult.authenticated && authResult.role === requiredRole;
}

/**
 * V√©rifie si l'utilisateur est admin
 */
export function isAdmin(authResult: AuthResult): boolean {
  return hasRole(authResult, "admin");
}

/**
 * V√©rifie si l'utilisateur est v√©t√©rinaire
 */
export function isVet(authResult: AuthResult): boolean {
  return hasRole(authResult, "vet");
}
